const parseDmcColors = () => {
    const lines = rawDmcData.split(/\r?\n/);
    const colors = [];

    for (const line of lines) {
        const trimmed = line.trim();
        if (!trimmed) continue;
        const match = trimmed.match(/^(\S+)\s+(.+?)\s+(\d+)\s+(\d+)\s+(\d+)\s+([0-9a-fA-F]{6})$/);
        if (!match) continue;
        const [, floss, name, r, g, b, hex] = match;
        colors.push({
            floss,
            name,
            r: Number(r),
            g: Number(g),
            b: Number(b),
            hex: hex.toLowerCase()
        });
    }

    return colors;
};

export const dmcColors = parseDmcColors();
